import 'es5';
import demo from '../template/demo.pug';
class Demo {
    constructor(){

    }
    init(){
        this.render();
    }
    render(){
        let domHtml = demo({
            data: 1
        });
        $("#container").html(domHtml);
    }
}
new Demo().init();




